function App() {
  const [open, setOpen] = React.useState(false);
  const [cardOpen, setcardOpen] = React.useState(false);

  const [selectedValue, setSelectedValue] = React.useState(null);

  const [purchase, setPurchase] = React.useState([]);

  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
    setSelectedValue(null);
  };
  const handleCardClose = () => {
    setcardOpen(false);
  };
  const clickHandler = (e) => {
    if (e.flavour) {
      handleClickOpen();
      setSelectedValue(e); //  selectedValue
    } else {
      setPurchase([...purchase, e]);
    }
  };
  const getDom = (name) => {
    return arrayData[name].map((item, i) => {
      return (
        <Box
          width="33%"
          height="14%"
          onClick={() => clickHandler(item)}
          display={"flex"}
          position={"relative"}
          marginBottom={"1rem"}
          key={item.priceLabel + String(i)}
        >
          <Box
            width={"70%"}
            display={"flex"}
            alignItems={"center"}
            justifyContent={"center"}
            border={"2px solid black"}
            borderRadius={"50%"}
            overflow={"hidden"}
          >
            <img src={IcedTea} height="100%" />
          </Box>
          <Box
            position={"absolute"}
            padding={"0.125rem"}
            height={"3rem"}
            width={"8rem"}
            display={"flex"}
            alignItems={"center"}
            justifyContent={"center"}
            top={"50%"}
            right={0}
            border={"2px solid #000"}
            borderRadius={"0.25rem"}
            style={{
              backgroundColor: "yellow",
              transform: "translate(0%, -40%)",
            }}
          >
            <Typography fontSize={" 1rem"}></Typography>
            <Typography fontSize={"1.2rem"} fontWeight={700}>
              {item.priceLabel}
            </Typography>
          </Box>
          <Box
            position={"absolute"}
            display={"flex"}
            bottom={"0.2rem"}
            padding={" 0.0625rem 0.375rem"}
            fontSize={"1rem"}
            style={{ background: "#fff" }}
            border={"0.125rem solid #000"}
            // width={'8rem'}
            borderRadius={"0.25rem"}
            height={"2rem"}
            lineHeight={"2rem"}
            fontWeight={600}
          >
            {item.product_description}
          </Box>
        </Box>
      );
    });
  };
  const setClick = (e) => {
    setPurchase([...purchase, e]);
  };
  useEffect(() => {
    console.log(purchase, "purchase");
  }, [purchase]);
  return (
    <Box height={"100vh"} display="flex" flexDirection="column">
      <Box
        display={"flex"}
        height={"90%"}
        padding={" 1rem"}
        style={{ flexWrap: "wrap", overflowY: "auto" }}
      >
        {snacks.map((item) => getDom(item))}
      </Box>

      <SimpleDialog
        setClick={setClick}
        selectedValue={selectedValue}
        open={open}
        onClose={handleClose}
      />

      <CardDialog
        open={cardOpen}
        setPurchase={setPurchase}
        onClose={handleCardClose}
        purchase={purchase}
      />
      <Box
        height={"10%"}
        display={"flex"}
        alignItems={"center"}
        justifyContent={"center"}
      >
        <Badge badgeContent={purchase.length} color="primary">
          <AddShoppingCartIcon fontSize={"2rem "} />

          <Box
            sx={{ width: "6rem", height: "1.5rem" }}
            onClick={() => setcardOpen(!cardOpen)}
          >
            
          </Box>
        </Badge>
      </Box>
    </Box>
  );
}

export default App;
